@font-face {
  font-family: 'ProximaNova';
  src: url('/src/fonts/ProximaNova-Regular.eot');
  src: local('Proxima Nova Regular'), local('ProximaNova-Regular'),
    url('/src/fonts/ProximaNova-Regular.eot?#iefix') format('embedded-opentype'),
    url('/src/fonts/ProximaNova-Regular.woff') format('woff'),
    url('/src/fonts/ProximaNova-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'ProximaNova';
  src: url('/src/fonts/ProximaNova-Semibold.eot');
  src: local('Proxima Nova Semibold'), local('ProximaNova-Semibold'),
    url('/src/fonts/ProximaNova-Semibold.eot?#iefix')
      format('embedded-opentype'),
    url('/src/fonts/ProximaNova-Semibold.woff') format('woff'),
    url('/src/fonts/ProximaNova-Semibold.ttf') format('truetype');
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: 'ProximaNova';
  src: url('/src/fonts/ProximaNova-Bold.eot');
  src: local('Proxima Nova Bold'), local('ProximaNova-Bold'),
    url('/src/fonts/ProximaNova-Bold.eot?#iefix') format('embedded-opentype'),
    url('/src/fonts/ProximaNova-Bold.woff') format('woff'),
    url('/src/fonts/ProximaNova-Bold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: 'Marcellus';
  src: url('../fonts/Marcellus-Regular.otf');
  src: local('☺'), url('../fonts//Marcellus-Regular.woff') format('woff'),
    url('../fonts/Marcellus-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
@font-face {
	font-family: 'Merriweather';
	src: url('../fonts/merriweather-blackitalic.eot');
